/* YouPay Custom CSS   */

@import '../base';

.youpay-button-group button {
  border-radius: 1.875rem;
}

.youpay-button-group .youpay-text-group .youpay-text {
  text-transform: initial;
  font-size: 1rem;
}

/*  YouPay Cart button  */
.c-cart-contents__footer .youpay-button-group[data-type="cart"] .youpay-button {
  border: 1px solid #e4987f;
  background: none;
  color: #e4987f;
  height: 3.5rem;
}

.c-cart-contents__footer .youpay-button-group[data-type="cart"] .youpay-button:hover {
  background: #e4987f;
  border: none;
}

@media (max-width: 46.8125rem) {
  .c-cart-contents__footer .youpay-button-group[data-type="cart"] .youpay-button {
    border: 1px solid #fff;
    color: #fff;
  }

  .c-cart-contents__footer .youpay-button-group[data-type="cart"] .youpay-button:hover {
    background: white;
    color: black;
    border: none;
  }
}

@media (min-width: 46.875rem) {
  .c-cart-contents__footer .youpay-button-group[data-type="cart"] {
    margin-bottom: 0.125rem;
  }
}

@media (max-width: 68.6875rem) {
  .youpay-callout-content .youpay-callout-message-before {
    font-size: 1rem !important;
  }
}

@media (min-width: 68.75rem) and (max-width: 103.0625rem) {
  .youpay-callout-content .youpay-callout-message-before {
    font-size: 1.0625rem !important;
  }
}

@media (max-width: 103.0625rem) {
  .c-cart-contents__footer .youpay-button-group[data-type="cart"] .youpay-button {
    height: 2.85rem;
  }
}

/*  YouPay Callout Message  */
.youpay-callout-container {
  display: flex;
  justify-content: center;
}

.youpay-callout-message-before {
  color: rgba(0, 0, 0, .4);
  font-family: GT Walsheim, sans-serif;
  font-size: 1.125rem !important;
  font-weight: 500 !important;
  letter-spacing: 0 !important;
}

.youpay-callout-content .youpay-light-logo path,
.youpay-callout-content svg:last-child,
.youpay-callout-container[data-theme="light"] .youpay-callout-content .youpay-logo-text {
  fill: rgba(0, 0, 0, 0.4);
}

.youpay-callout-content .youpay-light-logo path:nth-child(3) {
  fill: none;
}

.youpay-callout-content span {
  display: flex;
}

/*  Changes to existing CSS on theme  */
.c-product .c-product__purchase-wrapper {
  margin-bottom: 1.5rem;
}

.c-product__wishlist-container {
  margin-bottom: 1rem;
}

.c-product .c-product__payment-options {
  margin-bottom: 1.5rem;
}